	module.exports = {"data":{"markdownRemark":{"html":"<p>一人電車に乗ってて、辺りを見渡しても自分の他は誰も乗っていない。途中、駅に着いてドアが開くとなぜか東方キャラがたくさん入ってきた。確か窓から見える看板には前橋駅って書いてあった気がする。 自分は呆気にとられて入ってくるおにゃのこたちを眺めていたが、そこでふとあることを閃いて電車の長椅子の上に仰向けに寝転がってみる。頭から足までまっすぐ伸ばすと4人かけの長椅子はぜんぶ自分の体で占領される。 そして待つこと数秒、俺の狙い通り自分の体の上にどかどかと次々に 弾幕が撃ち込まれた。予想がはずれたと気付いた時には既に目が覚めていた。</p>","frontmatter":{"cover":null,"date":"2005-11-18T12:46:00+09:00","strDate":"2005/11/18","title":"今朝見た夢"}}},"pathContext":{"path":"/ja/blog/2005/11/18-今朝見た夢/","langKey":"ja"}}

